  text-decoration-style: wavy;
}

.navbar-dark .navbar-nav .active>.nav-link1, .navbar-dark .navbar-nav .nav-link1.active, .navbar-dark.navbar-nav .nav-link1.show, .navbar-dark .navbar-nav .show>.nav-link1{
